import { mapState } from "vuex";
import { deleteArticle } from "@/api/article";
export default {
  name: "ArticleMeta",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  computed: {
    ...mapState(["user"]),
  },
  methods: {
    async delArticle(article) {
      this.article.isDeleting = true;
      await deleteArticle(article.slug);
      this.article.isDeleting = false;
      this.$router.push("/");
    },
  },
};
