import { getComments, addComments } from "@/api/article";

export default {
  name: "ArticleComments",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      comments: [], // 文章列表
      comment: {
        body: "",
      }, // 评论
    };
  },
  mounted() {
    this.infoComments();
  },
  methods: {
    async infoComments() {
      const { data } = await getComments(this.article.slug);
      this.comments = data.comments;
    },
    async addComment() {
      const { data } = await addComments(this.article.slug, this.comment);
      this.comments.unshift(data.comment);
      this.comment.body = "";
    },
  },
};
